<template>
	<div class="c4w-section">
		<template v-if="error">
			<div class="container-sm">
				<img width="100%" src="images/helper/pablo-page-not-found.png" alt="" />
				<h2 :style="{ textAlign: 'center' }">Oops!</h2>
				<p :style="{ textAlign: 'center' }">{{ error }}</p>
				<p :style="{ textAlign: 'center' }">Try <a @click="logOut">Logging Out</a></p>
			</div>
		</template>

		<template v-if="user && !error">
			<div class="container-md">
				<!--				<pre>{{ user }}</pre>-->
				<h1>
					Welcome back, <span class="text-brand-primary">{{ user.first_name }}</span>
				</h1>
				<h3>General Profile Information</h3>
				<a-row :gutter="[0, 50]">
					<a-col>
						<strong>Account information</strong>
						<a-form-model :model="user" :rules="validation" ref="editForm">
							<a-row multiline :gutter="[15, 10]">
								<a-col :xs="24" :md="12">
									<a-form-model-item>
										<a-input :disabled="!canEdit" v-model="user.first_name" />
									</a-form-model-item>
								</a-col>
								<a-col :xs="24" :md="12">
									<a-form-model-item>
										<a-input :disabled="!canEdit" v-model="user.last_name" />
									</a-form-model-item>
								</a-col>
								<a-col :xs="24">
									<a-form-model-item>
										<a-input :disabled="!canEdit" v-model="user.company" />
									</a-form-model-item>
								</a-col>
							</a-row>
							<a-divider></a-divider>
							<template v-if="canEdit">
								<a-button
									:loading="isLoading"
									type="primary"
									@click="updateUser"
									:style="{ marginRight: '0.5rem' }"
									>Save</a-button
								>
								<a-button @click="toggleEditting">Cancel</a-button>
							</template>
							<template v-else>
								<a-button @click="toggleEditting">Edit Info</a-button>
							</template>
						</a-form-model>
					</a-col>
					<a-col>
						<strong>Account information</strong>
						<a-form-item>
							<a-input disabled :defaultValue="user.email" />
						</a-form-item>
						<div>
							<span>Status: <a-badge status="success" :text="user.status"></a-badge></span>
						</div>
						<div>
							<small class="text-brand-primary">
								<em>These details cannot be changed at the moment.</em>
							</small>
						</div>
					</a-col>

					<a-divider></a-divider>

					<a-col>
						<h3>Cards Liked</h3>
						<a-row type="flex" align="middle" justify="center" :gutter="[20, 20]">
							<a-col :xs="12" :md="6" :xl="4">
								Hello
							</a-col>
							<a-col :xs="12" :md="6" :xl="4">
								Hello
							</a-col>
							<a-col :xs="12" :md="6" :xl="4">
								Hello
							</a-col>
							<a-col :xs="12" :md="6" :xl="4">
								Hello
							</a-col>
							<a-col :xs="12" :md="6" :xl="4">
								Hello
							</a-col>
						</a-row>
					</a-col>

					<a-divider></a-divider>

					<a-col>
						<h3>Cards Created</h3>
					</a-col>
				</a-row>
			</div>
		</template>
	</div>
</template>

<script>
import { mapActions } from "vuex";
// const sgMail = require("@sendgrid/mail");

export default {
	data() {
		return {
			// editForm: this.$form.createForm(this, { name: "editUserInfo" }),
			isLoading: false,
			user: null,
			validation: {
				first_name: [{ required: true, message: "This field is required" }],
				last_name: [{ required: true, message: "This field is required" }],
				company: [{ required: true, message: "This field is required" }]
			},
			error: false,
			canEdit: false
		};
	},
	beforeMount() {
		this.$client
			.getMe({
				fields: "*.*"
			})
			.then((res) => {
				this.user = res.data;
			})
			.catch((err) => {
				this.$message.error(err.message);
				this.logOut();
			});
	},
	methods: {
		...mapActions(["startLoading", "doneLoading", "updateAuthStatus"]),
		toggleEditting() {
			this.canEdit = !this.canEdit;
		},
		updateUser() {
			this.isLoading = true;

			this.$refs.editForm.validate((valid) => {
				if (valid) {
					this.$client
						.updateUser(this.user.id, {
							first_name: this.user.first_name,
							last_name: this.user.last_name,
							company: this.user.company
						})
						.then(() => {
							this.$message.success("Updated your info!");
						})
						.catch(() => {
							this.$message.error("Something went wrong, try again!");
						})
						.then(() => {
							this.canEdit = false;
							this.isLoading = false;
						});
				} else {
					this.$message.error("Something went wrong, try again!");
				}
			});
		},
		logOut() {
			this.$client
				.logout()
				.then(() => {
					this.$message.success("Logging you out..");
				})
				.catch(() => {
					this.$message.error("Something went wrong, redirecting to homepage!");
				})
				.then(() => {
					window.localStorage.removeItem("token");
					this.updateAuthStatus(false);
					this.$router.push({ name: "home" });
				});
		}
	}
};
</script>
